import React, { Component } from 'react'
import Breadcrumb from '../../../Commons/Breadcrumb/Breadcrumb'
import { Form, Input, Button, message, Spin, Row, Col } from 'antd'
import Fetch from '../../../Commons/Fetch/Fetch'
import UploadFile from '../../../Commons/Upload/Upload';
import './Publish.css'

const FormItem = Form.Item;
const { TextArea } = Input;

class Publish extends Component {
  state = {
    spinning: false,
    loading: false,
    record: '',
    description: '',
    filePath: '',
    tree: ''
  }

  componentDidMount() {
    const { match } = this.props
    const { appId } = match.params
    const isNumber = !isNaN(parseInt(appId)) && isFinite(appId)
    if (appId && isNumber) { 
      this.setState({ spinning: true, appId }, () => this.getLastDeployment(appId))
    } else {
      message.warning(`ID`)
    }
  }
  /**
   * 
   */
  getLastDeployment(appId) {
    Fetch.getJSON(`/api/project/${appId}/lastDeployment`).then(
      res => {
        if (res) {
          this.setState({...res, spinning: false})
        } else {
          this.setState({spinning: false})
        }
      }
    ).catch( e => {
      e && this.dealError(e)
    })
  }
  /**
   * 
   */
  getRecord(id) {
    Fetch.getJSON(`/api/deployment/${id}/record`).then(
      res => {
        this.setState({record: res.text}, () => {
          if (res && res.status === 'UNKOWN') {
            setTimeout(() => {
              this.getRecord(id)
            }, 1000)
          } else {
            this.setState({loading: false})
          }
        })
      }
    ).catch( e => {
      e && this.dealError(e)
    })
  }
  /**
   * 
   */
  handleSubmit = e => {
    e.preventDefault()
    const { appId, filePath } = this.state
    
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let result = {}
        result.projectId = parseInt(appId)
        result.description = values.description
        result.filePath = filePath
        this.publish(result)
      }
    });
  }
  /**
   * 
   */
  publish(params) {
    this.setState({loading: true}, () => {
      Fetch.postJSON(`/api/deployment/publish`, params).then(
        res => {
          this.setState({spinning: false}, () => this.getRecord(res))
        }
      ).catch( e => {
        e && this.dealError(e)
      })
    })
  }
  /**
   * 
   */
  dealError(e) {
    let msg = e && e.message || ''
    if (e && e.code === 403) {
      msg = `${msg}, `
      setTimeout(() => {
        window.location.href = `/web/login`
      }, 3000)
    }
    message.error(msg)
    this.setState({ spinning: false })
  }
  /**
   * 
   */
  onUploadFile(values) {
    const state = Object.assign({}, this.state, values)
    this.setState({...state})
  }

  render() {

    const routes = [
      { path: '/apps/list', name: '' },
      { path: '', name: '' }
    ]
    const { getFieldDecorator } = this.props.form
    const { loading, spinning, record, filePath, tree, description } = this.state 

    return (
      <div>
        <Breadcrumb routes={routes} />
        <Spin spinning={spinning}>
          <Row type="flex" className="publish-container">
            <Col span={10}>
              <Form onSubmit={this.handleSubmit}>
                <FormItem
                  colon={false}
                  label=""
                  extra="xxx"
                >
                  {getFieldDecorator('description', {
                    initialValue: description,
                    validateTrigger: ['onChange', 'onBlur'],
                    rules: [{ required: true, message: '' }],
                  })(
                    <Input placeholder='' disabled={loading} />
                  )}
                </FormItem>

                <UploadFile onUploadFile={this.onUploadFile.bind(this)} loading={loading} />

                <div className="directory-box">
                  <div className="directory-header">$ {filePath}</div>
                  <TextArea value={tree} autosize={{minRows: 5, maxRows: 15}} disabled className="directory-content" />
                </div>
                
                <Button type="primary" htmlType="submit" className="publish-btn" loading={loading}></Button>

              </Form>
            </Col>
            <Col span={12} offset={2}>
              <div className="publish-label"></div>
              <TextArea value={record} autosize={{minRows: 5}} disabled className="publish-record" />
            </Col>
          </Row>
        </Spin>
      </div>
    )
  }
}

export default Form.create()(Publish)


